<template>
  <div class="editData">
    <div class="divider">
      <!-- elementui -->
      <el-divider>
        <h1 class="title"></h1>
      </el-divider>
    </div>
    <!-- form -->
    <el-form
      :model="data"
      :rules="rules"
      ref="ruleForm"
      label-width="80px"
      class="demo-ruleForm"
      label-position="left"
    >
      <el-form-item label="">
        <el-input v-model="data.user_name" disabled></el-input>
      </el-form-item>

      <el-form-item label="" prop="user_password">
        <el-input type="password" v-model="data.user_password"></el-input>
      </el-form-item>

      <el-form-item label="" prop="user_nickName">
        <el-input v-model="data.user_nickName"></el-input>
      </el-form-item>

      <el-form-item label="" prop="user_sex">
        <el-radio v-model="data.user_sex" label="1"></el-radio>
        <el-radio v-model="data.user_sex" label="0"></el-radio>
      </el-form-item>

      <el-form-item label="" prop="user_email">
        <el-input v-model="data.user_email"></el-input>
      </el-form-item>

      <el-form-item label="" prop="user_birthday">
        <el-date-picker
          v-model="data.user_birthday"
          type="date"
          placeholder=""
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-form-item>

      <el-form-item label="" prop="user_address">
        <el-cascader
          v-model="data.user_address"
          :options="cities"
          :placeholder="data.user_address"
        ></el-cascader>
      </el-form-item>

      <el-form-item label="" prop="user_avatar">
        <el-upload
          class="avatar-uploader"
          action="http://localhost:3000/api/upload"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="data.user_avatar" :src="data.user_avatar" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          ></el-button
        >
        <el-button @click="quitEdit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    // 
    let validatePassword = (rule, value, callback) => {
      let regWord = /[`!#$%^&*()_+<>?:"{}\\/;'[\]#|[\] \u4e00-\u9fa5]/i;
      let regPassword = /[\w,.@]{3,16}/;
      if (!value) {
        return callback(new Error(""));
      } else if (regWord.test(value)) {
        return callback(new Error(""));
      } else if (!regPassword.test(value)) {
        return callback(
          new Error(
            "316@,."
          )
        );
      } else {
        callback();
      }
    };

    //  
    let validateEmail = (rule, value, callback) => {
      let reg = /^([\w-]+)(?:\.([\w-]+))*?@([a-zA-Z0-9]+)(?:\.([a-zA-Z]+))+$/;
      if (!value) {
        return callback(new Error(""));
      } else if (!reg.test(value)) {
        return callback(new Error(""));
      } else {
        callback();
      }
    };

    //  
    let validateNickName = (rule, value, callback) => {
      let regWord = /[`!#$%^&*@,.()+<>?:"{}\\/;'[\]#|[\]]/i;
      let regNickName = /[a-zA-Z0-9_\u4e00-\u9fa5]{3,9}/;
      if (!value) {
        return callback(new Error(""));
      } else if (regWord.test(value)) {
        return callback(new Error(""));
      } else if (!regNickName.test(value)) {
        return callback(
          new Error(
            "39"
          )
        );
      } else {
        callback();
      }
    };

    //  
    let validateBirthday = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      } else {
        callback();
      }
    };
    //  
    let validateAddress = (rule, value, callback) => {
      if (value.length === 0) {
        return callback(new Error(""));
      } else {
        callback();
      }
    };

    //  
    let validateAvatar = (rule, value, callback) => {
      if (!this.data.user_avatar) {
        return callback(new Error(""));
      } else {
        callback();
      }
    };

    return {
      cities: [],
      rules: {
        user_password: [
          {
            required: true,
            validator: validatePassword,
            trigger: "blur"
          }
        ],
        user_nickName: [
          {
            required: true,
            validator: validateNickName,
            trigger: "blur"
          }
        ],
        user_sex: [
          {
            required: true,
            trigger: "blur"
          }
        ],
        user_email: [
          {
            required: true,
            validator: validateEmail,
            trigger: "blur"
          }
        ],
        user_birthday: [
          {
            required: true,
            validator: validateBirthday,
            trigger: "blur"
          }
        ],
        user_address: [
          {
            required: true,
            validator: validateAddress,
            trigger: "change"
          }
        ],
        user_avatar: [
          {
            required: true,
            validator: validateAvatar,
            trigger: "blur"
          }
        ]
      }
    };
  },
  props: {
    data: {
      type: Object,
      required: true,
      default() {
        return {};
      }
    }
  },
  methods: {
    /**
     * 
     */
    initData() {
      setTimeout(() => {
        this.data.user_sex = this.data.user_sex.toString();
      }, 300);
      this.initCities();
    },

    /**
     * 
     */
    initCities() {
      this.$axios({
        method: "get",
        url: "/getCities"
      })
        .then(res => {
          if (res.data.state === 0) {
            this.cities = JSON.parse(res.data.cityData[0].cities);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    /**
     * 
     */
    //     
    handleAvatarSuccess(res) {
      if (res.state === 0) {
        this.data.user_avatar = res.imgUrl;
      }
    },

    // 
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },

    /**
     * 
     */
    submitForm(formName) {
      this.$nextTick(() => {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.$axios({
              method: "post",
              url: "/updateUserData",
              data: {
                data: this.data
              }
            })
              .then(res => {
                if (res.data.state === 0) {
                  this.$message.success("");
                  // 
                  this.$emit("colseEdit");
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.error("");
          }
        });
      });
    },
    /**
     * 
     */
    quitEdit() {
      this.$confirm(", ?", "", {
        type: "warning"
      })
        .then(() => {
          // 
          this.$emit("colseEdit");
          // form
          this.$refs["ruleForm"].resetFields();
          this.data.user_avatar = "";
        })
        .catch(err => console.log(err));
    }
  },
  watch: {
    data: {
      // user_address
      handler(val) {
        if (typeof val.user_address == "object") {
          let str = "";
          val.user_address.forEach(item => {
            str += item;
          });
          val.user_address = str;
        }
      },
      deep: true
    }
  },
  mounted() {
    this.initData();
  }
};
</script>

<style scoped lang="scss">
.editData {
  .divider {
    padding-bottom: 1rem;
    .title {
      font-size: 1.2rem;
    }
  }
  .demo-ruleForm {
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
      border: 1px dashed #d9d9d9;
    }
    .avatar {
      width: 178px;
      height: 178px;
      display: block;
    }
  }
}
</style>
